@import "./../../style/varable.scss";
.button{
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #fff;
  border: 1px solid #dcdfe6;
  text-align: center;
  outline: none;
  margin: 0;
  transition: .1s;
  font-weight: 500;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
}
.button+.button{
  margin-left: 10px;
}
.button.default:hover{
  color: #fff;
  border-color: #909399;
  background-color: #909399;
}
.button.default:active{
  background: #82848a;
  border-color: #82848a;
  color: #fff;
}



.button.primary{
  color: #fff;
  background-color: #909399;
  border-color: #909399;
}

.button.primary:hover{
  background: #a6a9ad;
  border-color: #a6a9ad;
  color: #fff;
}
.button.primary:active{
  background: #82848a;
  border-color: #82848a;
  color: #fff;
}